import {
  ListItemButton,
  ListItemText,
  Typography,
  ListItemIcon,
  Button,
  IconButton,
} from "@mui/material";
import Accordion, { AccordionSummary, AccordionDetails } from "./Accordion";
import SearchEdit from "./SearchEdit";
import CircleIcon from "@mui/icons-material/Circle";
import InfoOutlinedIcon from "@mui/icons-material/InfoOutlined";
import ArrowBackIosRoundedIcon from "@mui/icons-material/ArrowBackIosRounded";
import MenuIcon from "@mui/icons-material/Menu";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import "./style.scss";
import { useState, useRef, useEffect } from "react";

export default (props) => {
  const [slipped, setSlipped] = useState(false);
  const [opened, setOpened] = useState(false);
  const prevSlipped = useRef(false);
  const timer = useRef(null);
  const panelHeader = useRef();
  const panelContent = useRef();
  const originPanelWidth = useRef(0);
  const [modalInfo, setModalInfo] = useState("");
  const [curApiId, setCurApiId] = useState(null);
  const [searchText, setSearchText] = useState("");

  const handleSlide = () => {
    if (timer.current) {
      clearInterval(timer.current);
      timer.current = null;
    }
    timer.current = setInterval(handlePanelMove, 10);
    setTimeout(() => {
      clearInterval(timer.current);
    }, 2500);
    if (!slipped) {
      originPanelWidth.current = props.getPanelSizes(0);
    } else {
      console.log(
        originPanelWidth.current,
        props.getPanelSizes(0) + props.getPanelSizes(1)
      );
      if (
        originPanelWidth.current >
        props.getPanelSizes(0) + props.getPanelSizes(1)
      )
        originPanelWidth.current =
          props.getPanelSizes(0) + props.getPanelSizes(1);
    }
    prevSlipped.current = slipped;
    setSlipped((slipped) => !slipped);
    props.setSlipped(!prevSlipped.current);
  };

  const handleClickAPIButton = (e, api) => {
    setCurApiId(api.id);
    props.setCurApi(api);
  };

  const setPanelCss = (attrName, attrVal) => {
    panelHeader.current.style[attrName] = attrVal;
    panelContent.current.style[attrName] = attrVal;
  };

  const handlePanelMove = () => {
    let step = 2;
    if (!prevSlipped.current) {
      if (props.getPanelSizes(0) >= step) {
        props.setPanelSizes([
          props.getPanelSizes(0) - step,
          props.getPanelSizes(1) + step,
          props.getPanelSizes(2),
        ]);
      } else {
        props.setPanelSizes([
          0,
          props.getPanelSizes(1) + props.getPanelSizes(0),
          props.getPanelSizes(2),
        ]);

        clearInterval(timer.current);
        timer.current = null;

        setPanelCss("display", "fixed");
      }
      setPanelCss("filter", "blur(2px)");
      setPanelCss("pointerEvents", "none");
      setPanelCss("zIndex", -1);
    } else {
      if (props.getPanelSizes(0) < originPanelWidth.current) {
        props.setPanelSizes([
          props.getPanelSizes(0) + step,
          props.getPanelSizes(1) - step,
          props.getPanelSizes(2),
        ]);
      } else {
        props.setPanelSizes([
          originPanelWidth.current,
          props.getPanelSizes(1) -
            (originPanelWidth.current - props.getPanelSizes(0)),
          props.getPanelSizes(2),
        ]);
        clearInterval(timer.current);
        timer.current = null;
      }
      setPanelCss("filter", null);
      setPanelCss("display", "relative");
      setPanelCss("pointerEvents", "initial");
      setPanelCss("zIndex", 0);
    }
  };

  const handleInfo = (e, info) => {
    e.stopPropagation();
    setOpened(true);
    setModalInfo(info);
  };

  const handleDialogClose = () => {
    setOpened(false);
  };

  const handleSearch = (e) => {
    setSearchText(e.target.value);
  };

  const findApiByText = (apiGroup) => {
    for (let api of apiGroup.apis) {
      if (api.title.toLowerCase().includes(searchText.toLowerCase()))
        return true;
    }
    return false;
  };

  return (
    <div className="panel">
      <div className="panel-header" ref={panelHeader}>
        <SearchEdit onChange={handleSearch} value={searchText} />
      </div>
      <div className="panel-content" ref={panelContent}>
        {props.data.map(
          (apiGroup, index) =>
            findApiByText(apiGroup) && (
              <Accordion key={index} defaultExpanded={true}>
                <AccordionSummary>
                  <Typography>{apiGroup.title}</Typography>
                  {apiGroup.info && (
                    <InfoOutlinedIcon
                      onClick={(e) => handleInfo(e, apiGroup.info)}
                    />
                  )}
                </AccordionSummary>
                <AccordionDetails>
                  {apiGroup.apis.map(
                    (api, subIndex) =>
                      api.title
                        .toLowerCase()
                        .includes(searchText.toLowerCase()) && (
                        <ListItemButton
                          className="api-item-button"
                          key={api.id}
                          onClick={(e) => handleClickAPIButton(e, api)}
                          selected={
                            curApiId == api.id ||
                            (curApiId == null && index == 0 && subIndex == 0)
                              ? true
                              : false
                          }
                        >
                          <ListItemIcon>
                            <div
                              dangerouslySetInnerHTML={{
                                __html: `<svg viewBox="0 0 24 24">${api.icon}</svg>`,
                              }}
                            />
                          </ListItemIcon>
                          <ListItemText primary={api.title}></ListItemText>
                        </ListItemButton>
                      )
                  )}
                </AccordionDetails>
              </Accordion>
            )
        )}
        <Dialog
          open={opened}
          onClose={handleDialogClose}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
          fullWidth={true}
          className={`theme-${props.theme}`}
          maxWidth="sm"
        >
          <DialogTitle id="alert-dialog-title">{"Description"}</DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-description">
              {modalInfo}
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button onClick={handleDialogClose} autoFocus>
              Close
            </Button>
          </DialogActions>
        </Dialog>
      </div>
      <IconButton
        className="slide-button"
        onClick={handleSlide}
        style={{ zIndex: 10 }}
      >
        {!slipped ? (
          <ArrowBackIosRoundedIcon sx={{ fontSize: "small" }} />
        ) : (
          <MenuIcon sx={{ fontSize: "small" }} />
        )}
      </IconButton>
    </div>
  );
};
