import { InputAdornment, TextField } from "@mui/material"
import SearchIcon from '@mui/icons-material/Search';
import './style.scss';

export default (props) => {
    return (
        <TextField
            variant="outlined"
            size="small"
            placeholder="Search endpoints"
            className="textField"
            autoComplete="off"
            {...props}
            InputProps={{
                startAdornment: (
                    <InputAdornment position="start">
                        <SearchIcon />
                    </InputAdornment>
                )
            }}
        />
    )
}