import CircleIcon from '@mui/icons-material/Circle';
import { Typography } from '@mui/material';
import "./style.scss"

export default ({ data: api }) => {
    return (
        <div className="document-panel">
            <div className="document-panel-header">
                {api && (
                    <div className='document-panel-title'>
                        <span dangerouslySetInnerHTML=
                            {
                                { __html: `<svg viewBox="0 0 24 24">${api.icon}</svg>` }
                            }
                        />
                        <Typography className='api-title'>{api.title}</Typography>
                    </div>
                )
                }
            </div>
            <div className='document-panel-content'>
                {api && (
                    <div dangerouslySetInnerHTML={{ __html: api.contentHtml }} />
                )}
            </div>
        </div>
    )
}