import { useEffect, useRef, useState } from "react";
import Split from "react-split";
import APICodeSamplePanel from "./APICodeSamplePanel";
import APIDocumentPanel from "./APIDocumentPanel";
import APIListPanel from "./APIListPanel";
import "./style.scss";

export default (props) => {
  const [slipped, setSlipped] = useState(false);
  const [curApi, setCurApi] = useState(null);
  const splitRef = useRef();

  const data = props.data ? props.data : [];
  const theme =
    props.theme == "dark" || props.theme == "light" ? props.theme : "dark";

  useEffect(() => {
    splitRef.current.split.pairs[0].gutter.style.pointerEvents = slipped
      ? "none"
      : "initial";
  }, [slipped]);

  useEffect(() => {
    for (let apiGroup of data) {
      for (let api of apiGroup.apis) {
        setCurApi(api);
        return;
      }
    }
  }, []);

  const setPanelSizes = (sizes) => {
    splitRef.current.split.setSizes(sizes);
  };

  const getPanelSizes = (i) => {
    let sizes = splitRef.current.split.getSizes();
    return parseFloat(sizes[i]);
  };

  return (
    <Split
      className={`split theme-${theme}`}
      gutterSize={2}
      minSize={[0, 0, 0]}
      maxSize={2200}
      snapOffset={0}
      ref={splitRef}
    >
      <APIListPanel
        data={data}
        setSlipped={setSlipped}
        setCurApi={setCurApi}
        theme={theme}
        setPanelSizes={setPanelSizes}
        getPanelSizes={getPanelSizes}
      />
      <APIDocumentPanel data={curApi} />
      <APICodeSamplePanel data={curApi} />
    </Split>
  );
};
