import "./App.css";
import APIDocumentation from "./components/APIDocumentation";
import apiGroupList from "./data/apidata";
import { useState } from "react";
import { Button } from "@mui/material";

function App() {
  const [theme, setTheme] = useState("dark");

  const toggleTheme = () => {
    setTheme((theme) => (theme == "dark" ? "light" : "dark"));
  };

  return (
    <div className="App">
      <div className="theme-bar">
        <Button
          className="theme-button"
          variant="contained"
          onClick={toggleTheme}
        >
          Change theme
        </Button>
      </div>
      <APIDocumentation data={apiGroupList} theme={theme} />
    </div>
  );
}

export default App;
